<template>
  <v-container>
    <v-row v-if="topic!=''">
      <v-col cols="12">
        <span class="text-h5">{{ topic.title }}</span>
        <v-divider></v-divider>
      </v-col>

      <v-col cols="12">
        <span class="mr-10">
          <v-avatar size="30" color="blue">
            <img
              v-if="topic.createdBy.avatar"
              :src="topic.createdBy.avatar"
            />
            <span v-else class="text-h6 white--text">
              {{ topic.createdBy.username[0].toUpperCase() }}
            </span>
          </v-avatar>
          {{ topic.createdBy.username }}
        </span>

        {{ topic.createdAt }}
      </v-col>
      <v-col cols="12">
        <div v-html="topic.content"></div>
        <v-divider></v-divider>
      </v-col>
      <v-col cols="12">
        <span> 查看： {{ topic.views }} ； 评论数：{{ topic.reply }} </span>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import TopicService from "@/service/topic_service.js";
import moment from "moment"; // 导入日期时间处理的模块
import marked from "marked"; // 导入 Markdown 渲染模块
export default {
  name: "home-topic-detail",
  data: () => ({
    topic: "",
    //
  }),
  mounted() {
    this.getTopicById(this.$route.params.id);
  },
  methods: {
    getTopicById(id) {
      TopicService.getTopicById(id)
        .then((resp) => {
          this.topic = this.fromJson(resp);
          console.log(this.topic);
        })
        .catch((error) => {});
    },

    // 处理读取到的一条记录，用来显示
     fromJson(item) {
      return {
        id: item.id, // 系统字段直接获取
        category: item.get("category")
          ? {
              text: item.get("category").get("title"),
              value: item.get("category").id,
            }
          : "",
        title: item.get("title"), // 自定义的字段使用 get() 来获取
        content: marked(item.get("content")),
        level: item.get("level"),
        views: item.get("views"),
        reply: item.get("reply"),
        tags: item.get("tags"),
        // 使用 moment 格式化日期时间
        createdAt: moment(item.createdAt).format("lll"),
        updatedAt: moment(item.updatedAt).format("lll"),
        // 创建者
        createdBy: item.get("createdBy")
          ? this.getUser(item.get("createdBy"))
          : "",
        // 最后修改者
        lastEditor: item.get("lastEditor")
          ? this.getUser(item.get("lastEditor"))
          : "",
      };
    },

    // 获取用户表中各个字段的值
    getUser(user) {
      // console.log(user);
      return {
        id: user.id,
        username: user.get("username"),
        email: user.get("email"),
        avatar: user.get("avatar"),
        mobilePhoneNumber: user.get("mobilePhoneNumber"),
        nickname: user.get("nickname"),
      };
    },
  },
  computed: {
    //
  },
};
</script>

